.filters {
  position: relative; }
  .filters__show-btn {
    font-weight: 450;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: #0569FF;
    cursor: pointer;
    user-select: none; }
    @media only screen and (min-width: 769px) {
      .filters__show-btn:hover {
        text-decoration: underline; } }
    .filters__show-btn img {
      width: 20px;
      height: 20px;
      display: block;
      object-fit: contain;
      flex-shrink: 0;
      margin-right: 12px; }
  .filters__dropdown {
    top: calc(100% + 16px);
