.fdrop {
  position: absolute;
  width: 100%;
  display: none;
  z-index: 10;
  background: #EDF0F9;
  box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.2); }
  .fdrop--active {
    display: block; }
  .fdrop__close {
    padding: 0;
    outline: 0;
    border: none;
    cursor: pointer;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px;
    width: 20px;
    height: 20px;
    display: block; }
    .fdrop__close img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .fdrop__inner {
    padding: 32px 24px 16px; }
  .fdrop__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: 0 -16px; }
  .fdrop__item {
    flex-basis: 279px;
    max-width: 279px;
    padding: 0 16px;
    margin-bottom: 24px; }
  .fdrop__title {
    font-weight: 450;
    font-size: 14px;
    line-height: 135%;
    color: #000000;
    margin-bottom: 16px; }
  .fdrop .vsb-main {
    width: 100%; }
    .fdrop .vsb-main button {
      position: relative;
      padding: 0 32px 0 12px !important;
      height: 30px !important;
      box-sizing: border-box;
      width: 100% !important; }
      .fdrop .vsb-main button .title {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 135%;
        color: #878E97;
        margin: 0;
        display: block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis; }
      .fdrop .vsb-main button .caret {
        width: 10px !important;
        height: 10px !important;
        position: absolute;
        right: 11px !important;
        top: 50% !important;
        transform: translateY(-50%);
        margin: 0 !important;
        border: none !important;
        background: url("../images/fdown.svg") center center no-repeat; }
    .fdrop .vsb-main .multi li:not(.grouped-option)::after,
    .fdrop .vsb-main .multi li:not(.grouped-option)::before {
      display: inline-block;
      box-sizing: content-box; }

.fdrop__range-slider {
  width: 480px;
  /* Match this to the SVG's x2 value */
  margin: auto;
  text-align: center;
  position: relative;
  height: 6em; }
  .fdrop__range-slider input[type="number"] {
    border: 1px solid #ddd;
    text-align: center;
    font-size: 1.6em; }
  .fdrop__range-slider input[type="number"]:invalid,
  .fdrop__range-slider input[type="number"]:out-of-range {
    border: 2px solid #ff6347; }
  .fdrop__range-slider input[type="range"] {
    -webkit-appearance: none;
    width: 100%; }
  .fdrop__range-slider input[type="range"]:focus {
    outline: none; }
  .fdrop__range-slider input[type="range"]:focus::-webkit-slider-runnable-track {
    background: #2497e3; }
  .fdrop__range-slider input[type="range"]:focus::-ms-fill-lower {
    background: #2497e3; }
  .fdrop__range-slider input[type="range"]:focus::-ms-fill-upper {
    background: #2497e3; }
  .fdrop__range-slider input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    background: #2497e3;
    border-radius: 1px;
    box-shadow: none;
    border: 0; }
  .fdrop__range-slider input[type="range"]::-webkit-slider-thumb {
    z-index: 2;
    position: relative;
    box-shadow: 0px 0px 0px #000;
    border: 1px solid #2497e3;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #a1d0ff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -7px; }
  .fdrop__range-slider input[type="range"]::-moz-range-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    background: #2497e3;
    border-radius: 1px;
    box-shadow: none;
    border: 0; }
  .fdrop__range-slider input[type="range"]::-moz-range-thumb {
    z-index: 2;
    position: relative;
    box-shadow: 0px 0px 0px #000;
    border: 1px solid #2497e3;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #a1d0ff;
    cursor: pointer; }
  .fdrop__range-slider input[type="range"]::-ms-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    color: transparent; }
  .fdrop__range-slider input[type="range"]::-ms-fill-lower,
  .fdrop__range-slider input[type="range"]::-ms-fill-upper {
    background: #2497e3;
    border-radius: 1px;
    box-shadow: none;
    border: 0; }
  .fdrop__range-slider input[type="range"]::-ms-thumb {
    z-index: 2;
    position: relative;
    box-shadow: 0px 0px 0px #000;
    border: 1px solid #2497e3;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #a1d0ff;
    cursor: pointer; }
  .fdrop__range-slider svg,
  .fdrop__range-slider input[type="range"] {
    position: absolute;
    left: 0;
    bottom: 0; }
